import React from 'react'
import "./Todo.css"
function TodoList(props) {
    const handleClick =(e)=>{
       const newList= props.list.filter((item)=>{
            return item.id!==e;
        })
        props.setList(newList)
    }
    return (
        <div>
            {
            props.list.map((item)=>{
               return(
                    <div className="todo">
                        <h3>{item.item}</h3>
                        <button onClick={()=>handleClick(item.id)}>-</button>
                    </div>
                    
                )
            })
            }
        </div>
    )
}

export default TodoList

